import axios from "axios";

const instanse = axios.create({
    withCredentials: true,
    baseURL: "http://localhost:8080"
});

export const API = {

    //
    setAuth(username, password) {
        return instanse
            .post("/login", {
                username,
                password
            })
            .then(response => {
                return response.data;
            })
            .catch(error => {
                return error;
            });
    },

    // 
    checkAuth() {
        return instanse
            .get("/api/check")
            .then(response => {
                return response.data;
            })
            .catch(error => {
                return error;
            });
    },

    //logout
    setLogout() {
        return instanse
            .get("/logout")
            .then(response => {
                return response.data;
            })
            .catch(error => {
                return error;
            });
    },

    //
    setRegister(username, email, password) {
        return instanse
            .post("/register", {
                username,
                email,
                password
            })
            .then(response => {
                return response.data;
            })
            .catch(error => {
                return error;
            });
    },

    // 
    getMain() {
        return instanse
            .get("/api/main/im")
            .then(response => {
                return response.data;
            })
            .catch(error => {
                return error;
            });
    },

    //
    getMove(direction) {
        return instanse
            .get("/api/main/move?direction=" + direction)
            .then(response => {
                return response.data;
            })
            .catch(error => {
                return error;
            });
    },

    //  
    getAttack(targetId) {
        if(targetId == undefined) {
            targetId = 0;
        }
        return instanse
            .get("/api/fight/attack?targetId=" + targetId)
            .then(response => {
                return response.data;
            })
            .catch(error => {
                return error;
            });
    },

    //  
    getFightRefresh() {
        return instanse
            .get("/api/fight/refresh")
            .then(response => {
                return response.data;
            })
            .catch(error => {
                return error;
            });
    },

    //     
    setHit(abilityId, targetId) {
        return instanse
            .get("/api/fight/hit?abilityId=" + abilityId + "&targetId=" + targetId)
            .then(response => {
                return response.data;
            })
            .catch(error => {
                return error;
            });
    },

    //     
    getAbility() {
        return instanse
            .get("/api/fight/ability")
            .then(response => {
                return response.data;
            })
            .catch(error => {
                return error;
            });
    },
}